export default ".sky {\r\n  position: relative;\r\n  width: 400px;\r\n  height: 200px;\r\n  background-color: #87ceeb; /* Sky blue */\r\n  overflow: hidden;\r\n}\r\n\r\n.bird {\r\n  width: 30px;\r\n  height: 15px;\r\n  background-color: #ffffff; /* White color for bird */\r\n  position: absolute;\r\n  border-radius: 50%; /* Oval shape for bird */\r\n  animation: moveBird 10s linear infinite;\r\n  top: 50%;\r\n  transform: translateY(-50%);\r\n}\r\n\r\n.bird:before,\r\n.bird:after {\r\n  content: \"\";\r\n  width: 15px;\r\n  height: 5px;\r\n  background-color: #ffffff;\r\n  position: absolute;\r\n  top: 50%;\r\n  transform: translateY(-50%);\r\n  border-radius: 50%; /* Oval shape for bird wings */\r\n  animation: flapWings 0.5s ease-in-out infinite;\r\n}\r\n\r\n.bird:before {\r\n  left: -15px;\r\n}\r\n\r\n.bird:after {\r\n  right: -15px;\r\n}\r\n\r\n.bird1 {\r\n  animation-delay: 0s;\r\n  left: -35px;\r\n}\r\n\r\n.bird2 {\r\n  animation-delay: 3s;\r\n  left: -35px;\r\n}\r\n\r\n.bird3 {\r\n  animation-delay: 6s;\r\n  left: -35px;\r\n}\r\n\r\n@keyframes moveBird {\r\n  0% {\r\n    left: -35px; /* Initial position - just out of view */\r\n  }\r\n  100% {\r\n    left: 100%; /* Move out of container view */\r\n  }\r\n}\r\n\r\n@keyframes flapWings {\r\n  0%,\r\n  100% {\r\n    transform: translateY(-50%);\r\n  }\r\n  50% {\r\n    transform: translateY(-60%);\r\n  }\r\n}\r\n";